import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { format } from 'date-fns';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextInputForm from '@/components/forms/TextInputForm';
import { DateInputForm } from '@/components/forms/DateInputForm';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import { AddBeneficiaryFormValues, AddBeneficiarySchema } from '../schemas/insurances/add-beneficiary-schema';
import { InsuranceRelatives } from '@/types/insurance.types';

interface Props {
    insuranceId: string;
    relative?: InsuranceRelatives;
    setShow: (value: boolean) => void;
}

export default function AddBeneficiaryForm({ insuranceId, setShow, relative }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);

    const defaultValues = useMemo(
        () => ({
            insurance_id: insuranceId,
            birth_date: relative?.birth_date ? new Date(relative.birth_date) : new Date(),
            name: relative?.name || '',
            amount: relative?.amount || '',
            phone: relative?.phone || '',
            identity_number: relative?.identity_number || '',
            relationship: relative?.relationship || '',
        }),
        [insuranceId, relative],
    );

    const form = useForm<AddBeneficiaryFormValues>({
        resolver: zodResolver(AddBeneficiarySchema),
        defaultValues,
    });

    const onSubmit = async (data: AddBeneficiaryFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        const formatedData = {
            ...data,
            birth_date: format(data.birth_date, 'yyyy-MM-dd'),
            insurance_id: insuranceId,
        };
        try {
            await apiPost({
                endpoint: relative
                    ? endpoints.insuranceRelatives.update + relative.id
                    : endpoints.insuranceRelatives.create,
                body: formatedData,
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تم اضافة البدل بنجاح');
            setShow(false);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-2 gap-x-6">
                        {/* <TextInputForm name="insurance_id" label="التأمين" placeholder="1234567890" /> */}
                        <TextInputForm name="name" label="الاسم" placeholder="محمد احمد" />
                        <TextInputForm name="phone" label="رقم الهوية" placeholder="215698423" />
                        <TextInputForm
                            name="identity_number"
                            type="number"
                            label="رقم الهوية"
                            placeholder="215698423"
                        />
                        <TextInputForm name="relationship" label="العلاقة" placeholder="اب" />
                        <TextInputForm name="amount" label="المبلغ" placeholder="1000" />
                        <DateInputForm name="birth_date" label="تاريخ الميلاد" placeholder="2004/04/30" />
                    </div>
                    <TextAreaInputForm
                        className="flex flex-col min-h-[157px]"
                        name="note"
                        label="ملاحظات"
                        placeholder="اكتب ملاحظات اضافية"
                    />
                    <div className="flex flex-row gap-4 mt-12">
                        <SecondaryButton
                            onClick={() => setShow(false)}
                            type="button"
                            className="w-full text-base leading-5 tracking-normal py-[10px] font-medium"
                        >
                            إلغاء
                        </SecondaryButton>
                        <PrimaryButton
                            type="submit"
                            className="w-full text-base leading-5 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الحفظ...' : 'تقديم الطلب'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
