import { useCallback, useState } from 'react';
import { DataTableToolbar } from './data-table-toolbar';
import { columns } from './columns';
import { DataTable } from '@/components/tables/customTable/data-table';
import { Message } from '@/types/message.types';

export default function InsuranceCertificateRequestList({ employeeInsuranceData }: { employeeInsuranceData: any }) {
    const [data, setData] = useState<Message[]>([]);
    const [pageCount, setPageCount] = useState(0);
    const [totalCount, setTotalCount] = useState(0);
    const [isLoading, setIsLoading] = useState(false);

    // Use useCallback to ensure the function reference is stable
    const fetchData = useCallback(async () => {
        setIsLoading(true);
        try {
            setData([employeeInsuranceData]);
            setPageCount(Math.ceil(employeeInsuranceData.length / 10));
            setTotalCount(employeeInsuranceData.length);
        } catch (error) {
            console.error('Error fetching data:', error);
        } finally {
            setIsLoading(false);
        }
    }, []);

    return (
        <div className="mt-6 mb-20 rounded-lg border border-[#E8E7EA] shadow-[0px_4px_4px_-1px_#0C0C0D0D,0px_4px_4px_-1px_#0C0C0D1A]">
            <DataTable
                columns={columns}
                data={data}
                pageCount={pageCount}
                totalCount={totalCount}
                onFetchData={fetchData}
                DataTableToolbar={DataTableToolbar}
                isLoading={isLoading}
            />
        </div>
    );
}
