import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import EmployeeInsuranceData from './insuranceBeneficiariesList/EmployeeInsuranceData';
import InsuranceBeneficiariesList from './insuranceBeneficiariesList/InsuranceBeneficiariesList';
import InsuranceStatistics from './insuranceBeneficiariesList/InsuranceStatistics';
import { endpoints } from '@/lib/axios/axios-global';

export default function InsuranceInfo() {
    const [employeeInsuranceData, setEmployeeInsuranceData] = useState<any>(null);

    useEffect(() => {
        const fetchEmployeeInsuranceData = async () => {
            const response = await apiGet({ endpoint: endpoints.insurance.employee });
            if (response) {
                setEmployeeInsuranceData(response.data?.data);
            }
        };

        fetchEmployeeInsuranceData();
    }, []);
    return (
        <div className="w-full mt-6">
            <InsuranceStatistics />
            {employeeInsuranceData && (
                <>
                    <EmployeeInsuranceData employeeInsuranceData={employeeInsuranceData} />
                    <InsuranceBeneficiariesList
                        insuranceId={employeeInsuranceData?.id}
                        insuranceBeneficiariesList={employeeInsuranceData?.relatives}
                    />
                </>
            )}
        </div>
    );
}
