import { ColumnDef, Row } from '@tanstack/react-table';
import { InsuranceRelatives } from '@/types/insurance.types';
import { EditIcon, Trash2Icon } from 'lucide-react';
import { useState } from 'react';
import AddBeneficiary from '../popups/AddBeneficiary';
import DeletePopup from '@/components/Popups/DeletePopup';
import TrashIcon from '@/components/icons/TrashIcon';
import { endpoints } from '@/lib/axios/axios-global';
import { toast } from 'sonner';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<InsuranceRelatives> }) => {
    const [isShowPopup, setIsShowPopup] = useState(false);
    const [showDeletePopup, setShowDeletePopup] = useState(false);

    const handleDelete = () => {
        setShowDeletePopup(true);
    };

    const handleCancel = () => {
        setShowDeletePopup(false);
    };

    const handleSuccess = () => {
        toast.success('تم حذف الفرع بنجاح');
        setShowDeletePopup(false);
        row.original.refreshTable?.();
    };

    return (
        <>
            <div className="flex flex-row justify-center gap-6">
                <button onClick={() => setIsShowPopup(true)} className="cursor-pointer">
                    <EditIcon className="cursor-pointer text-primery-blue-400" />
                </button>
                <AddBeneficiary
                    insuranceId={row.original.insurance_id.toString()}
                    open={isShowPopup}
                    setShow={setIsShowPopup}
                    relative={row.original}
                />
                <button onClick={handleDelete}>
                    <TrashIcon className="text-[#E81E1E] cursor-pointer" />
                </button>
            </div>
            {showDeletePopup && (
                <DeletePopup
                    open={showDeletePopup}
                    text="هل أنت متاكد أنك تريد حذف هذا الفرع"
                    endpoint={endpoints.insuranceRelatives.delete + row.original.id}
                    handleCancel={handleCancel}
                    handleSuccess={handleSuccess}
                />
            )}
        </>
    );
};

