import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiGet, apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useEffect, useMemo, useState } from 'react';
import { Employee } from '@/types/employee.types';
import { format } from 'date-fns';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextInputForm from '@/components/forms/TextInputForm';
import { AddAllowanceFormValues } from '@/features/humanResources/forms/schemas/addAllowance/add-allowance-schema';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import SearchableSelectInputForm from '@/components/forms/SearchableSelectInputForm';
import SelectInputForm from '@/components/forms/SelectInputForm';
import { AddRequestFormValues, AddRequestSchema } from '../schemas/insurances/add-request-schema';

interface Props {
    employee?: Employee | null;
    setAddAllowance: (value: boolean) => void;
}

export default function CertificateRequestForm({ employee, setAddAllowance }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [employees, setEmployees] = useState<{ name: string; value: string }[]>([]);
    const [employeesError, setEmployeesError] = useState<string | null>(null);

    const defaultValues = useMemo(
        () => ({
            employee_id: '',
            certificate_type: '',
            certificate_purpose: '',
            note: '',
        }),
        [employee],
    );

    const form = useForm<AddRequestFormValues>({
        resolver: zodResolver(AddRequestSchema),
        defaultValues,
    });

    const onSubmit = async (data: AddRequestFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors
        const formatedData = {
            ...data,
        };
        try {
            await apiPost({
                endpoint: endpoints.insurance.create,
                body: formatedData,
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تم اضافة الطلب بنجاح');
            setAddAllowance(false);
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        const fetchEmployees = async () => {
            try {
                const response = await apiGet({ endpoint: endpoints.employee.list });

                const options = response.data.data.data.map((employee: Employee) => {
                    return { name: employee.name, value: String(employee.id) };
                });
                setEmployees(options);
                setEmployeesError(null);
            } catch (error) {
                setEmployeesError('Error fetching employees. Please try again later.');
            }
        };

        fetchEmployees();
    }, []);

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-2 gap-x-6">
                        <SearchableSelectInputForm
                            name="employee_id"
                            placeholder="محمد احمد"
                            label="الاسم"
                            options={employees}
                            error={employeesError}
                            data={true}
                        />
                        <SelectInputForm
                            name="certificate_type"
                            label="نوع الشهادة"
                            options={[
                                { value: 'high_school', name: 'الثانوية العامة' },
                                { value: 'bachelor', name: 'بكالوريوس' },
                                { value: 'master', name: 'ماجستير' },
                                { value: 'phd', name: 'دكتوراه' },
                            ]}
                            placeholder="مفردات راتب"
                        />
                        <TextInputForm
                            name="certificate_purpose"
                            label="الغرض من الشهادة"
                            placeholder="تقديم لجهة حكومية"
                        />
                    </div>
                    <TextAreaInputForm
                        className="flex flex-col min-h-[157px]"
                        name="note"
                        label="ملاحظات"
                        placeholder="اكتب ملاحظات اضافية"
                    />
                    <div className="flex flex-row gap-4 mt-12">
                        <SecondaryButton
                            onClick={() => setAddAllowance(false)}
                            type="button"
                            className="w-full text-base leading-5 tracking-normal py-[10px] font-medium"
                        >
                            إلغاء
                        </SecondaryButton>
                        <PrimaryButton
                            type="submit"
                            className="w-full text-base leading-5 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الحفظ...' : 'تقديم الطلب'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
