import UnderlineHeading from '@/components/heading/UnderlineHeading';
import adel from '@/assets/imgs/user.png';
import Cookies from 'js-cookie';

export default function Heading() {
    const employee = JSON.parse(Cookies.get('user') || '{}');
    return (
        <div className="mb-6">
            <UnderlineHeading text="التأمينات" />
            <div className="w-full py-6 ps-8 pe-[58px] bg-primery-blue-500 mt-6 rounded-2xl flex justify-between items-center">
                <div className="flex items-center gap-4">
                    <img src={adel} className="w-[172px] h-[172px] rounded-full object-cover" alt="" />
                    <div className="flex flex-col gap-9">
                        <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                            الاسم : {employee?.name}
                        </p>
                        <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                            رقم المشترك: {employee?.id}
                        </p>
                        <div className="flex flex-row gap-[10px]">
                            <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                                حالة الاشتراك:
                            </p>
                            <span className="bg-[#DAF8E6] text-[#1A8245] text-lg font-medium leading-[115%] px-[6px] rounded-md flex items-center">
                                نشط
                            </span>
                        </div>
                    </div>
                </div>
                <div className="flex flex-col gap-9">
                    <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                        تاريخ بدء الاشتراك : 2025/05/01
                    </p>
                    <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                        تاريخ انتهاء الاشتراك : 2026/04/30
                    </p>
                    <p className="text-[22px] tracking-normal leading-[150%] text-white-blue-50">
                        مدة الاشتراك : 1 سنة
                    </p>
                </div>
            </div>
        </div>
    );
}
