import { useEffect, useState } from 'react';
import InsuranceCertificateRequestList from './insuranceCertificateRequestList/InsuranceCertificateRequestList';
import InsuranceRequestsStatistics from './insuranceCertificateRequestList/InsuranceRequestsStatistics';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';

export default function InsuranceCertificateRequest() {
    const [employeeInsuranceData, setEmployeeInsuranceData] = useState<any>(null);

    useEffect(() => {
        const fetchEmployeeInsuranceData = async () => {
            const response = await apiGet({ endpoint: endpoints.insurance.employee });
            if (response) {
                setEmployeeInsuranceData(response.data?.data);
            }
        };

        fetchEmployeeInsuranceData();
    }, []);
    return (
        <div className="w-full mt-6">
            <InsuranceRequestsStatistics />
            {employeeInsuranceData && <InsuranceCertificateRequestList employeeInsuranceData={employeeInsuranceData} />}
        </div>
    );
}
