import { z } from 'zod';

export const AddRequestSchema = z.object({
    employee_id: z.string().min(1, 'Employee id field is required.'),
    certificate_type: z.string().min(1, 'Certificate type field is required.'),
    certificate_purpose: z.string().min(1, 'Certificate purpose field is required.'),
    note: z.string().min(1, 'Note field is required.'),
});

export type AddRequestFormValues = z.infer<typeof AddRequestSchema>;
