import { z } from 'zod';

export const AddBeneficiarySchema = z.object({
    // insurance_id: z.any().min(1, 'Insurance id field is required.'),
    birth_date: z.date({
        required_error: 'Birth date is required',
    }),
    name: z.string().min(1, 'Name is required'),
    amount: z.string().min(1, 'Amount is required'),
    phone: z.string().min(1, 'Phone is required'),
    identity_number: z.string().min(1, 'Identity number is required'),
    relationship: z.string().min(1, 'Relationship is required'),
});

export type AddBeneficiaryFormValues = z.infer<typeof AddBeneficiarySchema>;
